<template>

    <div  class="reg-panel1">
        <div class="hd">
            会员注册
            <router-link to="/e/index">首页</router-link>
        </div>
        <div class="bd">

            <div v-if="!over" >
                <h2> <i class="fa fa-edit" style="font-size: 30px;color: red;"></i></h2>
                <div class="form-row">
                    <div class="label2">
                        <em>*</em>账号:
                    </div>
                    <div class="ct">
                        <input type="text" name="accountname" v-model="huiyuan.accountname"  v-validate="{required:true,messages:{required:'请输入账户名'}}" class="input"/>

                    </div>
                </div>
                <div class="form-row">
                    <div class="label2">
                        <em>*</em>密码:
                    </div>
                    <div class="ct">
                        <input type="password"  name="password" v-model="huiyuan.password"  v-validate="{required:true,messages:{required:'请输入密码'}}"  class="input"/>
                    </div>
                </div>


                <div class="form-row">
                    <div class="label2">
                        <em>*</em>确认密码:
                    </div>
                    <div class="ct">
                        <input type="password" name="password2" v-model="huiyuan.password2" v-validate="{required:true,equalTo:'#txtPassword',messages:{required:'请再次输入密码',equalTo:'两次密码不一致'}}" class="input"/>
                    </div>
                </div>

                <div class="form-row">
                    <div class="label2">
                        <em>*</em>姓名:
                    </div>
                    <div class="ct">
                        <input type="text" v-model="huiyuan.name" v-validate="{required:true,messages:{required:'请输入姓名'}}" name="name" class="input"/>
                    </div>
                </div>

                <div class="form-row">
                    <div class="label2">
                        <em>*</em>身份证号:
                    </div>
                    <div class="ct">
                        <input type="text" v-model="huiyuan.idcardno" v-validate="{required:true,idcardno:true, messages:{required:'请输入身份证号',idcardno:'请输入正确的身份证号'}}" name="idcardno" class="input"/>
                    </div>
                </div>
                <div class="form-row">
                    <div class="label2">
                        <em>*</em>邮箱:
                    </div>
                    <div class="ct">
                        <input type="text" v-model="huiyuan.email" v-validate="{required:true,email:true,messages:{required:'请输入邮箱',email:'请输入正确邮箱'}}" name="email" class="input"/>
                    </div>
                </div>
                <div class="form-row">
                    <el-button @click="registerHandler" class="reg-btn" type="danger" icon="el-icon-plus">注册</el-button>
                </div>



            </div>


            <div v-else class="result-guide-box">
                <div class="content clearfix">
                    <div class="icon-area">
                        <i class="fa fa-check-circle"></i>
                    </div>
                    <div class="text-area">
                        <strong>注册成功！</strong>
                        <router-link to="/admin/login">登录系统</router-link>
                    </div>
                </div>
            </div>



        </div>
    </div>


</template>

<script>
    import {VueUtil} from "../util/vueutil";

    export default {
        name: "register",
        data() {
            return {

                actiontype: 'save',
                over:false,
                errors: {},
                hostHead: this.$app.contextPath,
                "huiyuan": {
                    "accountname": "",
                    "password": "",
                    "email": "",
                    "idcardno": "",
                    "nickname": "",
                    "name": "",
                    "touxiang": "/upload/nopic.jpg",
                    "des": ""
                },

            }
        },
        methods: {

            async registerHandler() {

                let defaultOptions = {
                    url: "admin/huiyuan/save",
                    actionTip: "注册成功"
                };

                const  validRes=this.myValidator.valid(this,{isShowErrors:true});
                console.log("valRes",validRes);
                if (!validRes)
                    return ;
                let util = new VueUtil(this);
                let params = {...this.huiyuan};
                let {data: res} = await util.http.post(defaultOptions.url, params);
                if (res.stateCode <= 0) {
                    util.alert(res.des, '系统提示', {
                        confirmButtonText: '确定'
                    });
                    return;
                }
                util.message({
                    message: defaultOptions.actionTip,
                    type: 'success',
                    duration: 2000
                });
                this.over=true;
            }
        },
        created() {
            console.log("注册创建");
        }

    }
</script>

<style scoped>
    @import "./css/register.css";
    label.error{
        position: absolute;
        top: -2px;
    }
</style>
